import React, {Component} from 'react'
import classNames from 'classnames'
import Portal from 'react-portal'
import { Link } from 'react-router-dom'
import { createFakePath, isDirectory } from '../../tools'
import style from '../../styles/Documents.sass'
import button from '../../styles/buttons.scss'

import Page from './Page'
import Loader from './Loader'
import DocumentList from './DocumentList'
import DataList from './DataList'
import DocumentView from './DocumentView'
import ToolBoxOptions from './ToolBoxOptions'
import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'

import { STATUS_DONE, STATUS_LOAD, STATUS_ERROR, ROOT_PATH, VIEW_LIST, SORT_DATE, SORT_NAME, TYPE_FOLDER, TYPE_FILE } from '../../constants'

import { getDoctypeFromPath } from '../../scripts/dataFields.js'

export class Documents extends Component {
    
    constructor(props, context){
        super(props, context)

        this.state = {
            modalOpen: false,
            file: false,
            doctype: false
        }
    }

    componentWillMount () {
        const newPath = this.createRealPath(this.props)
        
        if(newPath !== this.props.path || this.props.fileCount !== this.props.files.length) this.props.onOpenFolder(newPath)

        this.checkForData(newPath)
    }

    componentWillReceiveProps (newProps) {
        const newPath = this.createRealPath(newProps)
        
        if(newProps.status === STATUS_ERROR) {
            if(newPath === newProps.path) return
            return newProps.onOpenFolder(newPath)
        }
        if(newProps.status === STATUS_LOAD) return

        if(newPath !== newProps.path || newProps.fileCount !== newProps.files.length) newProps.onOpenFolder(newPath)
        
        this.checkForData(newPath)            
    }

    createRealPath(props) {
        const param = props.match.params.path
        let newPath = ROOT_PATH
        if (param) newPath += '/' + param
        return newPath
    }

    checkForData(newPath) {
        const doctype = getDoctypeFromPath(newPath)
        if(doctype) {
            if(!this.state.doctype) {
                this.state.doctype = doctype
                this.props.onLoadData(doctype)
            } else if(this.state.doctype !== doctype) {
                this.props.onLoadData(doctype)                
            }
        } else {
            this.state.doctype = false
        }
    }

    openImporter() {
        this.props.onImporterClick(this.props.path)
        this.setState({modalOpen: true})
    }

    createBreadcrums() {
        const displayPath = this.props.location.pathname.substring(1);
        const foldersSplit = displayPath.split('/');
        const folders = [];

        if(foldersSplit.length > 1) {
            var root = foldersSplit[0];
            folders.push({
                path: root,
                fullPath: '/'+root
            });

            if(foldersSplit.length > 3) {
                var dot = foldersSplit[foldersSplit.length-3];
                folders.push({
                    path: '/...',
                    fullPath: '/'+foldersSplit.slice(0, foldersSplit.length-2).join('/')
                });

                var f = foldersSplit[foldersSplit.length-2];
                folders.push({
                    path: '/'+f,
                    fullPath: '/'+foldersSplit.slice(0, foldersSplit.length-1).join('/')
                });
            } else if(foldersSplit.length > 2) {
                var f = foldersSplit[foldersSplit.length-2];
                folders.push({
                    path: '/'+f,
                    fullPath: '/'+foldersSplit.slice(0, foldersSplit.length-1).join('/')
                });
            }

        }
        var lastFolder = (foldersSplit.length > 1 ? '/' : '')+foldersSplit[foldersSplit.length-1];

        return (
            <span className={style.breadcrums}>
                {folders.map((el, key) => (
                    <Link key={key} to={el.fullPath}>{el.path}</Link>
                ))}
                <strong>{lastFolder}</strong>
            </span>
        )
    }

    renderDocuments() {
        if(this.props.lastType !== TYPE_FOLDER && files.length > 0) return
        
        const { history, viewMode, files, folder, onSelect, onFileClick } = this.props

        return (
            <DocumentList
             view={viewMode}
             onClickItem={item => {
                let path
                if(isDirectory(item)) {
                    onFileClick(null) 
                    path = createFakePath(item.path)
                } else {
                    //check for simple file
                    onFileClick(item) 
                    path = createFakePath(folder.path + '/' + item.name)
                }

                console.log('Push path', path);
                history.push(path)    
             }}
             onSelectItem={item => onSelect(item)}>
                {files}
            </DocumentList>
        )
    }

    renderData() {
        if(!this.state.doctype) return

        const { history, viewMode, data, onSelectData } = this.props

        return (
            <DataList
            view={viewMode}
            onClickItem={item => {
                history.push('/data/'+item._id)
            }}
            onSelectItem={item => onSelectData(item)}>
                {data}
            </DataList>
        )
    }

    renderFileViewer() {
        if(this.props.lastType !== TYPE_FILE) return

        const { current } = this.props

        return (
            <DocumentView file={current}/>
        )
    }

    renderError() {
        return (
            <div>
                <h3>Error</h3>
                <p>Essayer plus trad</p>
            </div>
        )
    }

    render() {
        const { files, folder, fileCount, status, selectedCount, dataStatus, dataCount, selectedDataCount } = this.props

        const loading = status === STATUS_LOAD || dataStatus === STATUS_LOAD

        let empty = !loading && ( ( fileCount === 0 ) && ( this.state.doctype && dataCount === 0 ) )

        const title = this.createBreadcrums()

        //Create actions
        const importer = (<button onClick={() => this.openImporter()} className={classNames(button.button, button.default)} title="Importer"><i className="ion-ios-cloud-upload-outline"></i><span className={button.text}>IMPORTER</span></button>)
        const toolbox = (
            <ToolBoxOptions>
                <ul>
                    <li onClick={() => {
                        this.props.onChangeViewMode()
                    }}>
                        {this.props.viewMode === VIEW_LIST ? (
                            <i className="ion-navicon">VUE LISTE</i>
                        ) : (
                            <i className="ion-grid">VUE GRILLE</i>
                        )}
                    </li>
                    <li onClick={() => {
                        this.props.onNewFolder()
                    }}>
                        <i className="ion-ios-plus-empty">NOUVEAU DOSSIER</i>
                    </li>
                    <li onClick={() => {
                        this.props.onDownloadAll(this.props.files.slice(0))
                    }}>
                        <i className="ion-ios-cloud-download-outline">TOUT TELECHARGER</i>
                    </li>
                    <li onClick={() => {
                        //this.props.onRename()
                    }}>
                        <i className="ion-ios-compose-outline">RENOMMER</i>
                    </li>
                    <li onClick={() => {
                         //this.props.onMove() 
                    }}>
                        <i className="ion-ios-browsers-outline">DEPLACER</i>
                    </li>

                </ul>
            </ToolBoxOptions>
        )
        const filter = (
            <select defaultValue={this.props.filter} onChange={e => this.props.onChangeFilter(e.target.value)}>
                <option value={SORT_DATE}>TRIER PAR DATE</option>
                <option value={SORT_NAME}>TRIER PAR NOM</option>
            </select>
        )

        const actions = [importer,toolbox]

        if(selectedCount > 0 || selectedDataCount > 0) {
            const remove = (<button onClick={() => this.props.onRemove(files.filter(f => f.selected))} className={classNames(button.button, button.assertive)} title="Supprimer"><i className="ion-ios-trash-outline"></i><span className={button.text}>SUPPRIMER</span></button>)
            actions.unshift(remove)
        }
        if(selectedCount > 0) {
            const download = (<button onClick={() => this.props.onDownload(files.filter(f => f.selected))} className={classNames(button.button, button.stable)} title="Tlcharger"><i className="ion-ios-cloud-download-outline"></i><span className={button.text}>TELECHARGER</span></button>)
            actions.unshift(download)
        }

        let count = fileCount
        if(this.state.doctype) count += dataCount

        let documents, data, fileViewer, error
        if(!loading) {
            // no error
            if(this.props.status !== STATUS_ERROR && this.props.dataStatus !== STATUS_ERROR) {
                documents = this.renderDocuments()
                data = this.renderData()
                fileViewer = this.renderFileViewer()
            } else {
                error = this.renderError()
            }
        }

        return (
            <Page title={title} subtitle={count !== null && status != STATUS_ERROR && (count + " lment" + (count > 1 ? "s" : ""))} 
                actions={actions}
                onBackPress={this.props.match.params.path && (() => this.props.history.goBack())}
                options={[filter]}>

                <Loader display={loading}/>

                {empty && "Dossier vide"}

                {documents}
                {data}
                {fileViewer}
                {error}

                <Portal closeOnEsc isOpened={this.state.modalOpen} onClose={() => this.setState({modalOpen: false})}>
                    <Modal>
                        <Importer/>
                    </Modal>
                </Portal>

            </Page>
        )

    }
}

export default Documents


